.icons>div {
  text-align: center;
  display:flex;
  justify-content: center;
  flex-direction: column;
  min-height: 60px;
}

@media (min-width: 768px) {
  .icons>div {
    min-height: 90px;
  }
}

@media (min-width: 1024px) {
  .sizing{
    font-size: 7rem;
  }
}

@media (min-width: 1280px) {
  .sizing{
    font-size: 9rem;
  }
}

.icons i {
  display: inline-block;
}

